<template>
  <v-layout column>
    <!-- <Pageheader></Pageheader> -->
    <v-flex xs6 offset-xs3>
      <h1>{{signup}}</h1>
        <form
          name="tab-tracker-form"
          autocomplete="off">
          <v-text-field
            label=""
            v-model="name"
            :rules="nameRules"
            single-line
          ></v-text-field>
          <br>
          <v-text-field
            label=""
            type="password"
            v-model="password"
            :rules="passRules"
            autocomplete="new-password"
          ></v-text-field>
          <br>
          <v-text-field
            label="Email"
            v-model="email"
            single-line
          ></v-text-field>
          <br>
          <v-text-field
            label="        "
            v-model="description"
            single-line
          ></v-text-field>
        </form>
        <br>
        <div class="danger-alert" v-html="error" />
        <br>
        <v-btn v-if="name.length > 0 && password.length > 0"
          dark
          class="cyan"
          @click="register">
          {{signup}}
        </v-btn>
        <v-snackbar
        :timeout=5000
        v-model="snackbar"
        color='red'
      >{{ alreadyInUseMessage }}</v-snackbar>
    </v-flex>
  </v-layout>
</template>

<script>
import Pageheader from '@/components/pageheader';
import AuthenticationService from '@/services/AuthenticationService';

export default {
  data() {
    return {
      email: '',
      name: '',
      password: '',
      description: '',
      error: null,
      login: '',
      signup: '',
      snackbar: false,
      alreadyInUseMessage: '     .   .',
      nameRules: [
        v => !!v || 'To      .',
      ],
      passRules: [
        v => !!v || '      .',
        v => v.length > 7 || '       8 ,     .',
      ],
      emailRules: [
        v => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || ' email     .', // eslint-disable-line no-useless-escape
      ],
    };
  },
  components: {
    Pageheader,
  },
  methods: {
    async register() {
      try {
        const response = await AuthenticationService.register({
          name: this.name,
          password: this.password,
          email: this.email,
          description: this.description,
        });
        // console.log('response is:: ', response);
        if (response.data.result === 'success') {
          this.$router.push({
            name: 'login',
          });
        }
        if (response.data.result === 'in use') {
          this.snackbar = true;
        }
      } catch (error) {
        this.error = error.response.data.error;
      }
    },
  },
};
</script>



